import React from 'react'
import BackIcon from '../../commanComponent/BackIcon'
import { Link } from 'react-router-dom'
import { useSelector } from 'react-redux'
import { CustomTabs } from '../../reuseableComponents'
import { dateFormate, getBadgeColor, toTitleCase } from '../../../utils/common'

const ViewTaskDetail = () => {
    const {
        selectedTaskDetail
    } = useSelector(state => state.taskManagement)

    const taskDetailTabs = [
        { name: "All", id: "all" },
        { name: "History", id: "history" },
        { name: "Work Log", id: "work_log" },
    ]

    return (
        <div className="cardBox myRequest mb-3 employeeHistory">
            <div className='d-flex mb-3'>
                <div >
                    <Link to={"/task"}>
                        <BackIcon />
                    </Link>
                </div>
                <div>
                    <h5 className="ms-2 text-capitalize">
                        {selectedTaskDetail?.name}
                    </h5>
                </div>
                <div className='ms-2 mt-1'>
                    <span className='span-label'>Task.ID:{selectedTaskDetail?.task_id}</span>
                </div>
            </div>
            <div>

            </div>
            <div className='row'>
                <div className='col-8 '>
                    <h3 className='underlineHead'> Activity</h3>

                    <CustomTabs tabs={taskDetailTabs} type="taskDetailTabs" />
                    <div className="tab-content">
                        <div className="tab-pane fade active show" id="all">


                        </div>
                        <div className="tab-pane fade show" id="history">

                        </div>
                        <div className="tab-pane fade show" id="work_log">

                        </div>
                    </div>
                </div>
                <div className='col-md-4'>
                    <div className='row'>

                    </div>
                    <div className="border p-3 rounded-1">
                        <div className="row">
                            <div className="prtl d-flex mb-2 align-items-center justify-content-between">
                                <h6 className="m-0">Details</h6>
                            </div>
                            <hr style={{ width: '94%' }} />
                            <>
                                <div className="col-3 mb-2">
                                    <h6 className="gryslight fw-normal">Project :</h6>
                                </div>
                                <div className="col-9">
                                    <h6 className="fw-normal">{selectedTaskDetail?.project_name ||'-'}</h6>
                                </div>
                            </>
                            <>
                                <div className="col-3 mb-2">
                                    <h6 className="gryslight fw-normal">Title :</h6>
                                </div>
                                <div className="col-9">
                                    <h6 className="fw-normal">{selectedTaskDetail?.title || '-'}</h6>
                                </div>
                            </>
                            <>
                                <div className="col-3 mb-2">
                                    <h6 className="gryslight fw-normal">Task Type:</h6>
                                </div>
                                <div className="col-9">
                                    <h6 className="gryslight fw-normal">{selectedTaskDetail?.task_type_name||'-'}</h6>
                                </div>
                            </>
                            <>
                                <div className="col-3 mb-2">
                                    <h6 className="gryslight fw-normal">Priority:</h6>
                                </div>
                                <div className="col-9" style={{ display: 'inline-flex', paddingBottom: '5%' }}>
                                    <h6 className="gryslight fw-normal">

                                        <span
                                            className={`badge ${getBadgeColor(selectedTaskDetail?.task_priority)}`}
                                        >
                                            {selectedTaskDetail?.task_priority||'-'}
                                        </span>
                                    </h6>
                                </div>
                            </>
                            <>
                                <div className="col-3 mb-2">
                                    <h6 className="gryslight fw-normal">Assignee :</h6>
                                </div>
                                <div className="col-9" style={{ display: 'inline-flex', paddingBottom: '5%' }}>
                                    <div className="shortNames"> {toTitleCase(selectedTaskDetail?.emp_name) ||'-'}</div>
                                    <div>
                                        <h6 className="fw-normal" style={{ paddingTop: '2%' }}>&nbsp;{selectedTaskDetail?.emp_name}</h6>
                                    </div>
                                </div>
                            </>
                            <>
                                <div className="col-3 mb-2">
                                    <h6 className="gryslight fw-normal">Reporter :</h6>
                                </div>
                                <div className="col-9" style={{ display: 'inline-flex', paddingBottom: '5%' }}>
                                    <div className="shortNamesReporter"> {toTitleCase(selectedTaskDetail?.reporter_name) ||'-'}</div>
                                    <div>
                                        <h6 className="fw-normal" style={{ paddingTop: '2%' }}>&nbsp;{selectedTaskDetail?.reporter_name }</h6>
                                    </div>
                                </div>
                            </>
                            <>
                                <div className="col-3 mb-2">
                                    <h6 className="gryslight fw-normal">Watcher:</h6>
                                </div>
                                <div className="col-9">
                                    <h6 className="fw-normal">{selectedTaskDetail?.watcher ||'-'}
                                    </h6>
                                </div>
                            </>
                            <>
                                <div className="col-3 mb-2">
                                    <h6 className="gryslight fw-normal">Task Date:</h6>
                                </div>
                                <div className="col-9">
                                    <h6 className="fw-normal adre">{dateFormate(selectedTaskDetail?.start_date)||'-'}
                                    </h6>
                                </div>
                            </>
                            <>
                                <div className="col-3 mb-2 ">
                                    <h6 className="gryslight fw-normal">Due Date:</h6>
                                </div>
                                <div className="col-9 d-flex">


                                    <h6 className="fw-normal adre">{dateFormate(selectedTaskDetail?.end_date)||'-'}
                                    </h6>

                                    <span>
                                        <i className="fa fa-edit" aria-hidden="true"></i>
                                    </span>

                                    <span className='ms-1'>(
                                        <span style={{ color: "red" }}>1 day's late</span>)</span>
                                </div>
                            </>
                            <>
                                <div className="col-3 mb-2">
                                    <h6 className="gryslight fw-normal">Original Estimate:</h6>
                                </div>
                                <div className="col-9">
                                    <h6 className="fw-normal adre">{selectedTaskDetail?.original_estimate||'-'}
                                    </h6>
                                </div>
                            </>

                        </div>
                    </div>
                </div>

            </div>

        </div>
    )
}

export default ViewTaskDetail